import React, {useEffect} from 'react'
import {Link} from 'react-router-dom'
import { useDispatch,useSelector } from 'react-redux'
import { Row,Col, ListGroup, Image,Form, Button, Card } from 'react-bootstrap'
import Message from '../components/Message'
import { addToCart,removeFromCart } from '../actions/cartActions'
import { useLocation, useParams, useNavigate } from 'react-router-dom'

function CartScreen() {
    const {id} = useParams()

    const { search } = useLocation()
    const searchParams = new URLSearchParams(search)
    const qty = searchParams.has('qty') ? Number(searchParams.get('qty')) : 1

    const dispatch = useDispatch()

    const cart = useSelector(state =>state.cart)
    const {cartItems} = cart
    

    useEffect(() =>{
        if(id){
            dispatch(addToCart(id,qty))
        }
    },[dispatch, id, qty])
    
    const removeFromCartHandler = (id) =>{
        dispatch(removeFromCart(id))
    }

    const navigate = useNavigate()
    const checkoutHandler = () =>{
        navigate('/login?redirect=/shipping')
    }

    return (
        <Row>
            <Col md={8}>
                <h1>Shopping Cart</h1>
                {cartItems.length === 0?(
                    <Message variant='info'>Your cart is empty<Link to ='/' className='btn'>Go Back</Link></Message>
                ): (
                    <ListGroup variant='flush'>
                        {cartItems.map(item => (
                            <ListGroup.Item key={item.product}>
                                <Row>
                                    <Col md={2}>
                                        <Image src={item.image} alt={item.name} fluid rounded/>
                                    </Col>

                                    <Col md={3}>
                                        <Link to={`/product/${item.product}`} style={{ textDecoration: 'none', color: 'inherit' }}>{item.name}</Link>
                                    </Col>

                                    <Col md={2}>
                                        ${item.price}
                                    </Col>

                                    <Col md={3}>
                                        <Form.Control
                                            as="select" 
                                            value={item.qty}
                                            onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))}
                                            > {[...Array(item.countInStock).keys()].map((x) => (
                                            <option key= {x+1} value={x+1}>{x+1}</option>
                                            ))}
                                        </Form.Control>
                                    </Col>

                                    <Col md={1}>
                                        <Button onClick={()=> removeFromCartHandler(item.product)} type='button' variant='light'>
                                            <i className='fas fa-trash'></i>
                                        </Button>
                                    </Col>
                                </Row>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                )}
            </Col>

            <Col md={4}>
                <Card>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>
                            <h2>Subtotal ({cartItems.reduce((acc,item) => acc+item.qty, 0)}) items</h2>
                            ${cartItems.reduce((acc,item) => acc+item.qty * item.price, 0).toFixed(2)}
                        </ListGroup.Item>
                    </ListGroup>

                    <ListGroup.Item>
                        <Button variant="dark" size="lg" className="w-100" 
                                style={{ borderRadius: '0' }} 
                                disabled={cartItems.length === 0}
                                onClick={checkoutHandler}>
                            Proceed to Checkout
                        </Button>
                        
                    </ListGroup.Item>
                </Card>
            </Col>
        </Row>
    )
}

export default CartScreen